.slideout-menu {
  position: fixed;
  z-index: 8;
  top: 0;
  bottom: 0;
  display: none;
  overflow-y: scroll;
  width: 256px;
  min-height: 100vh;
  -webkit-overflow-scrolling: touch;
  background: #181a22;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}

.slideout-menu::-webkit-scrollbar {
  width: 0;
}

.slideout-menu-right {
  right: 0;
  overflow-x: hidden;
}

.slideout-menu-right ul {
  list-style: none;
  padding-left: 10px;
  padding-top: 35px;
  position: relative;
}

.slideout-menu-right li {
  padding-top: 35px;
  padding-left: 40px;
}

.slideout-menu-right a {
  color: #cdcdcd;
  font-size: 20px;
  font-weight: 100;
  transition: all 0.3s;
}

.slideout-menu-right .user ul {
  padding-top: 5px;
}

.slideout-menu-left {
  left: 0;
}

.slideout-panel {
  position: relative;
  z-index: 9;
  min-height: 100vh;
  background-color: #fff;
  will-change: transform;
}

.slideout-open {
  overflow: hidden;
}

.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: block;
}

.panel:before {
  content: '';
  display: block;
  background-color: transparent;
  transition: background-color 0.5s ease-in-out;
}

.panel-open:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

.menu-close {
  position: absolute;
  top: 20px;
  right: 10%;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.menu-close span {
  background-color: transparent;
  position: relative;
  top: 20px;
}

.menu-close span:after, .menu-close span:before {
  content: "";
  position: absolute;
  left: 0;
  top: -10px;
}

.menu-close span:after {
  content: '';
  top: 10px;
  width: 36px;
  height: 5px;
  background-color: #333333;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
  transform: rotate(-45deg) translate(9px, -8px);
}

.menu-close span:before {
  content: '';
  transform: rotate(45deg) translate(6px, 5px);
  width: 36px;
  height: 5px;
  background-color: #333333;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 2px;
}

